/** @jsx jsx */
import { jsx } from '@emotion/core';
import Page from 'components/Layout/Page';
import { Container, Grid } from '@material-ui/core';
import { DRI } from 'src/models';
import { useStaticQuery, graphql } from 'gatsby';
import DRICard from 'components/Cards/DriCard';

interface QueryResult {
  allDrisJson: {
    nodes: DRI[];
  };
}
const DRIs = () => {
  const {
    allDrisJson: { nodes: dris },
  }: QueryResult = useStaticQuery(graphql`
    query AllDRIsQuery {
      allDrisJson {
        nodes {
          name
          description
          members
        }
      }
    }
  `);

  return (
    <Page
      pageName="DRI"
      showTitle={true}
      description="     DRI.        DRI    !"
    >
      <Container>
        <Grid container spacing={3}>
          {dris.map((dri, index) => (
            <Grid item xs={12} md={3} key={index}>
              <DRICard dri={dri} />
            </Grid>
          ))}
        </Grid>
      </Container>
    </Page>
  );
};

export default DRIs;
