/** @jsx jsx */
import { jsx, css } from '@emotion/core';
import styled from '@emotion/styled';
import { Card, Grid, CardContent, CardHeader, Typography, Avatar, CardActionArea, makeStyles } from '@material-ui/core';
import AnimatedListItem from 'components/Animated/AnimatedListItem';
import { Tool } from 'src/models';
import { useAllMembers } from 'src/hooks/useAllMembers';
import { useMemo } from 'react';

interface Props {
  tool: Tool;
}
const ToolCard = ({ tool: { name, url, icon, description, dri } }: Props) => {
  const members = useAllMembers();
  const memberDri = useMemo(() => {
    return members.find(member => member.id === dri);
  }, [dri, members]);

  const style = makeStyles({
    avatar: {
      width: '2rem',
      height: '2rem',
    },
  })();

  return (
    <AnimatedListItem>
      <a
        href={url}
        target="_blank"
        rel="noopener noreferrer"
        css={css`
          text-decoration: none;
        `}
      >
        <Card>
          <CardActionArea>
            <CardHeader
              avatar={
                <Avatar
                  src={memberDri.profileImg.childImageSharp.fixed.src}
                  alt={memberDri.name.en}
                  className={style.avatar}
                />
              }
              title="DRI"
              subheader={memberDri.name.en}
            />
            <CardContent>
              <Grid container direction="column" alignItems="center">
                <Grid item component={ToolLogo} src={icon.childImageSharp.fixed.src} />
                <Grid
                  item
                  component={Typography}
                  gutterBottom={true}
                  css={css`
                    margin: 1rem 0;
                    font-weight: bold;
                  `}
                >
                  {name}
                </Grid>
                <Grid item component={Typography} variant="caption">
                  {description}
                </Grid>
              </Grid>
            </CardContent>
          </CardActionArea>
        </Card>
      </a>
    </AnimatedListItem>
  );
};

export default ToolCard;

const ToolLogo = styled.img`
  width: 3rem;
  height: 3rem;
  overflow: hidden;
`;
