/** @jsx jsx */
import { jsx } from '@emotion/core';
import { Router } from '@reach/router';
import ClientRoute from 'components/Utils/ClientRoute';
import Login from 'pages-client/login';
import Logout from 'pages-client/logout';
import Tools from 'pages-client/tools';
import Dris from 'pages-client/dris';

const ClientApp = () => (
  <Router basepath="/app">
    <ClientRoute path="/login" component={Login} permissions={['anonymous']} />
    <ClientRoute path="/logout" component={Logout} permissions={['user']} redirectTo="/" />
    <ClientRoute path="/tools" component={Tools} permissions={['user']} redirectTo="/app/login" />
    <ClientRoute path="/dris" component={Dris} permissions={['user']} redirectTo="/app/login" />
  </Router>
);

export default ClientApp;
