/** @jsx jsx */
import { jsx } from '@emotion/core';
import { PagePermission } from 'models/auth';
import { navigate } from 'gatsby';
import { useAuthState } from 'react-firebase-hooks/auth';
import firebase from 'gatsby-plugin-firebase';

function isAllowedPermission(isAuthorized: boolean, permissions: PagePermission[]) {
  const isUserPage = permissions.includes('user');
  const isAnonymousPage = permissions.includes('anonymous');
  const isFreePage = permissions.includes('all');

  const conditions = [isFreePage, isAuthorized && isUserPage, !isAuthorized && isAnonymousPage];

  return conditions.some(v => Boolean(v));
}

interface Props {
  component: any;
  permissions?: PagePermission[];
  redirectTo?: string;
}
const ClientRoute = ({ component: Component, permissions = ['all'], redirectTo = '/', ...rest }: Props) => {
  const [firebaseUser, loading] = useAuthState(firebase.auth());

  if (loading) {
    return null;
  }

  if (!isAllowedPermission(Boolean(firebaseUser), permissions)) {
    navigate(redirectTo);
  }

  return <Component {...rest} />;
};

export default ClientRoute;
